.admin-dashboard {
  text-align: center;
  padding: 20px;
}

h1 {
  font-size: 2em;
  margin-bottom: 20px;
  color: #333;
}

p {
  font-size: 1.2em;
  margin-bottom: 20px;
  color: #555;
}

.upload-section {
  margin: 20px 0;
}

.upload-button {
  background-color: #ff6f61;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
  transition: background-color 0.3s ease;
}

.upload-button:hover {
  background-color: #ff4c3b;
}

#file-upload {
  display: none;
}

p {
  font-size: 1em;
  margin-top: 10px;
  color: #333;
}
