/* Reset Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(45deg, #FF6F61, #FFD700, #8A2BE2, #00CED1);
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
  color: #333;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh;
}

@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

h1 {
  font-size: 2.5em;
  color: white;
  margin: 20px 0;
  text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.7);
  text-align: center;
}

/* Section Styles */
.section {
  width: 100%;
  max-width: 1200px;
  margin: 20px 0;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.section h2 {
  font-size: 1.8em;
  color: #FF6F61;
  margin-bottom: 15px;
  text-align: center;
}

/* Deals Section */
.deals {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 25px;
  justify-items: center;
}

.deal {
  background: linear-gradient(135deg, #FFDDC1, #FF9A8B);
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 15px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  max-width: 250px;
}

.deal img {
  width: 100%;
  max-width: 150px;
  height: auto;
  margin-bottom: 10px;
  border-radius: 5px;
}

.deal h3 {
  font-size: 1.2em;
  color: #333;
}

.deal p {
  font-size: 1em;
  margin: 10px 0;
}

.deal button {
  background: #FF6F61;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.deal:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.deal button:hover {
  background: #FF4C3B;
}

/* AI Section */
.ai-fun {
  text-align: center;
}

.ai-button {
  background: #FF6F61;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease;
  margin-top: 10px;
}

.ai-button:hover {
  background: #FF4C3B;
}

.joke {
  margin-top: 20px;
  font-size: 1.2em;
  font-style: italic;
  color: #333;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  h1 {
    font-size: 2em;
  }

  .deal {
    max-width: 100%;
  }

  .section {
    padding: 15px;
  }

  .deals {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  /* Admin Login Page */
  .login-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: linear-gradient(45deg, #FF6F61, #FFD700, #8A2BE2, #00CED1);
    background-size: 400% 400%;
    animation: gradientBG 15s ease infinite;
    color: white;
  }

  .login-page h1 {
    font-size: 2.5em;
    margin-bottom: 20px;
    text-shadow: 2px 2px 10px rgba(0, 0, 0, 0.7);
  }

  .login-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }

  .login-form input {
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ddd;
    border-radius: 5px;
  }

  .login-form button {
    padding: 10px;
    background: #FF6F61;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1em;
    cursor: pointer;
    transition: background 0.3s ease;
  }

  .login-form button:hover {
    background: #FF4C3B;
  }

  
}
